@import "../../assets/styles/breakpoints";

.about-page {
    padding-top: 2rem;
    min-height: 95vh;
    display: flex;
    flex: 0 1 auto;
    flex-direction: column;
    justify-content: center;

    @include breakpoint-small-down {
        padding-top: 3rem;
    }

    h1 {
        padding: 1rem 0;
        font-size: 3rem;
    }

    .content-grid {
        flex: auto;
    }

    .about-wrapper {
        .about-content {
            font-size: 0.875rem;
            line-height: 1.5rem;

            @include breakpoint-small-down {
                font-size: 1rem;
            }

            blockquote {
                background: #f9f9f9;
                border-left: 10px solid #ccc;
                margin: 1.5em 10px;
                padding: 0.5em 10px;
                quotes: "\201C""\201D""\2018""\2019";
            }
            blockquote:before {
                color: #ccc;
                content: open-quote;
                font-size: 4em;
                line-height: 0.1em;
                margin-right: 0.25em;
                vertical-align: -0.4em;
            }
            blockquote p {
                display: inline;
            }

            p {
                font-weight: 300;
                padding: 0.5rem 0;
            }

            .text-emoji {
                padding: 2rem 0;
                font-size: 1.5rem;
                font-weight: 300;
                text-align: center;
            }
        }
    }
}
